import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Observable } from 'rxjs';
import { AngularFirestore } from 'angularfire2/firestore';
import { Exam, Collections, ExamTypes } from '../../app/app.models';
import { map } from 'rxjs/operators';

@IonicPage()
@Component({
  selector: 'page-simulacros',
  templateUrl: 'simulacros.html',
})
export class SimulacrosPage {

  public exams$: Observable<any[]>
  public isOffline: boolean = false
  public downloadList: any[]

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private afs: AngularFirestore,
  ) {
  }

  ionViewDidLoad() {

    this.exams$ = this.afs.collection<Exam>(Collections.EXAM, ref => ref
      .where('type', '==', ExamTypes.SIMULACRO))
      .valueChanges()
      .pipe(
        /* map(list => list.filter(exam => moment(exam.date).isSameOrBefore(moment().endOf('day'))).reverse()), */
        map(list => list.filter(e => e.liberado).reverse()),
      )

    /* this.back.getSimuladoresHome().subscribe(data => this.sims = data.reverse(), err => {
      const list = JSON.parse(localStorage.getItem('zamnademy-cache-list'))
      this.downloadList = list ? list : []
      this.isOffline = true
    }) */
  }

}



